<template>
  <li class="nav-item">
    <router-link
      style="font-family: 'Abel', sans-serif; letter-spacing: 1px"
      class="nav-link mx-2 text-capitalize fs-5"
      :to="`/${check}`"
    >
      {{ text }}</router-link
    >
  </li>
</template>

<script>
export default {
  name: "navItem",
  props: {
    text: String,
  },
  computed: {
    check() {
      return this.text == "home" ? "" : this.text;
    },
  },
};
</script>

<style scoped>
nav a.router-link-exact-active {
  color: white;
}
</style>
